import logo from './logo.svg';
import './App.css';
import { Button, ButtonProps } from "@material-ui/core/";
import DirectoryCreateForm from './components/DirectoryCreateForm';
import OutlinedButtons from './components/Button'
import SimpleCard from './components/Card';
//import FullScreenDialog from './components/Directorydialog';
import { connect } from 'react-redux';
import { Component } from 'react';
import Directorylist from './components/Directorylist';
import UploadButtons from './components/upload_btn';
//import Button from '@material-ui/core/Button';




class Main extends Component{
  constructor(props) {
    super(props);
  }

  render(){
    return (
      <div className="App">

      

      <div class="ml-5 mr-5">
        <h1 class="text-center mt-5 mb-4"><span class="marker-animation">データの引継ぎ</span></h1>
        <h5 >ブラウザのURLデータを引き継ぐことができます。</h5>
        <h5 style={{marginTop:"0px",marginBottom:"40px"}}>
        <br />
        <u>データのエクスポート</u>
        <p></p>

        このブラウザのデータを引き継ぐ場合はメニューの「Save Data」からデータファイルをダウンロードして保存してください。
        <br />
        <p></p>
        <u>データのインポート</u>
        <br />
        <p></p>
        下のボタンから、別のブラウザでダウンロードしたファイルをアップロードしてください。
        アップロードしたファイルはサーバーには保存されません。
        また元のブラウザのデータは消去されず、新たにデータが追加されます。
        （同じ名前のリストは上書きされます）</h5>
        <UploadButtons />
      </div>

      <h1 class="text-center mt-5 mb-4"><span class="marker-animation">リストの作成</span></h1>
      <DirectoryCreateForm />
      <a id="directory"></a>
      <h1 class="text-center mt-5 mb-4"><span class="marker-animation">リスト一覧</span></h1>
      <Directorylist />
      {/*<SimpleCard />*/}
      </div>
    );
  }
}

export default connect((state)=>state)(Main);
