import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Button from '@material-ui/core/Button';
import IconButton from '@material-ui/core/IconButton';
import PhotoCamera from '@material-ui/icons/PhotoCamera';

const useStyles = makeStyles((theme) => ({
  root: {
    '& > *': {
      margin: theme.spacing(1),
    },
  },
  input: {
    display: 'none',
  },
}));

/* シーザー復号化 */
function decaesar(val, key) {
  console.log(val);
  var result = "";
  for (var i = 0; i < val.length; i++) {
    result += String.fromCharCode(val.charCodeAt(i) - key);
  }
  return decodeURIComponent(result) ;
}

window.addEventListener('load', () => {
      try {
        const f = document.getElementById('contained-button-file');
        f.addEventListener('change', evt => {
          const input = evt.target;

          const file = input.files[0];
          const reader = new FileReader();
          reader.readAsText(file);

          reader.onload = () => {
            console.log(reader.result);

            let data = reader.result;
            //暗号を復元する
            data = decaesar(data, 5)

            let word = "yourlistdateisdividedthisword";

            //ローカルストレージに保存するデータ数を取得
            var count = ( data.match( new RegExp( word, "g" ) ) || [] ).length/2;
            if(count == 0){
              alert("適切なファイルを選択してください。");
              return;
            }
            for (var i = 0; i < count; i++) {
              console.log(i+1);
              // 1．基準となる文字列の位置を取得
            	var index = data.indexOf(word);
            	// 2．基準文字列から前の文字列を切り出して表示
            	let data_1 = data.substring(0, index);
              data = data.replace( data_1 , "" )
              data = data.replace( word,"" )

              var index = data.indexOf(word);
              let data_2 = data.substring(0, index);
              data = data.replace( data_2 , "" )
              data = data.replace( word,"" )
              console.log(data_1);
              console.log(data_2);

              //リストの上書き
              var data_check = localStorage.getItem( data_1 ); // なかったらnullが返る
              if ( data_check ){
                  //data_check = JSON.parse( data_check );
                  if ( data_check && data_check.match(/newdir/)){
                      console.log("リストの上書き");
                      data_2 = data_check + "," + data_2;
                      data_2 = data_2.replace( "newdir,", "" );
                  } else {
                      console.log("リストではない");
                  }
              } else {
                  console.log('リストがない(使ってない)');
              }

              localStorage.setItem(data_1, data_2);
            }

            setTimeout(() => {
              alert("データをインポートしました。");
              window.location.reload(); // Execution 5sec
            }, 100);
          };
          });
      } catch (e) {
        alert("アップロードエラー");
      }
  });


export default function UploadButtons() {
  const classes = useStyles();

  return (
    <div className={classes.root} >
      <input
        className={classes.input}
        id="contained-button-file"
        multiple
        type="file"
        /*onChange = {() =>upload_data()}*/
      />
      <label htmlFor="contained-button-file" >
        <Button variant="contained" color="primary"
        component="span"
        size="large"
        >
          データファイルをアップロード
        </Button>
      </label>
    </div>
  );
}
